body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: blueviolet;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a:hover{
  cursor: pointer;
}

body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #fff;
  background: #ff5252;
  margin: 10px 30px;
}

#homepage {
  max-width: 800px;
  margin: 60px auto;
  padding: 10px 20px;
  background: #fdfdfd;
  border-radius: 4px;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.2);
}

input[type="text"] {
  display: block;
  width: 300px;
  margin: 20px 0;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ddd;
}

label {
  color: #ffffff;
}

.title {
  color: #fff;
  text-align: center;
  font-size: 48px;
}

#homepage h1 {
  text-align: center;
}

form {
  max-width: 300px;
  margin: 40px auto;
}

input[type="submit"] {
  background: #555;
  color: #fff;
  border: 0;
  padding: 10px;
  font-size: 18px;
  width: 200px;
  margin: 0 auto;
  display: block;
  cursor: pointer;
  border-radius: 4px;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  padding: 15px 0;
  background: #eee;
  list-style-type: none;
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
  border-radius: 4px;
}

span {

  display: inline-flex;
  margin: 0 8px;
}

span.dist {
  float: right;
  color: green;
}

span.true {
  border-radius: 10px;
  width: 10px;
  height: 10px;
  background: green;
}

span.false {
  border-radius: 10px;
  width: 10px;
  height: 10px;
  background: #ff5252;
}

button {
  background: green;
  border: 0;
  padding: 5px 10px;
  color: #fff;
  display: inline-block;
  border-radius: 4px;
  cursor: pointer;
}

button.false {
  display: none;
}
